.slider-image {
    width: 100%;
    height: 100vh;

    &__items {
        li {
            display: inline-block;

            img {
                width: 100%;
                height: 100vh;
            }
        }
    }
}

/*
Header section
*/
header.home-page {
    position: absolute;
    left: 0;
    right: 0;
}

.home-page__menu {
    display: flex;
    flex-direction: row;
    height: 4.25rem;

    &__buttons {
        background-color: $green;
        text-align: center;
        flex: 0;

        a {
            color: white;
            display: inline-block;
            font-size: 14px;
            padding: 25px;
        }
    }

    &__hover {
        background-color: $green;
        flex: 8;
        opacity: 0;

        &__list {
            padding: 0.875rem;

            li {
                display: inline-block;
                padding: 0.6875rem 1.25rem;

                a {
                    color: #2d7d27;
                    font-size: 0.875rem;
                }
            }

            li:last-child {
                margin-right: 0;
                float: right;

                a {
                    font-size: 1.375rem;
                }
            }

            li:hover {
                background-color: #46a13f;

                a {
                    color: white;
                }
            }

            li:last-child:hover {
                background-color: transparent;
                opacity: 0.9;
            }
        }
    }
}

/*Header details */
.home-page__details {
    display: flex;
    flex-direction: row;

    //logo
    &__logo {
        flex: 2;
        height: 110px;
        background-color: $dark;
        overflow: hidden;

        &__area {
            display: flex;
            justify-content: center;
            // padding-top: 20px;

            img {
                object-fit: contain;
                width: 100%;
            }
        }
    }

    // searchBAR
    &__searchBar {
        flex: 8;
        display: flex;
        flex-direction: column;

        &__up {
            // background-color: $blue;
            display: flex;
            flex-direction: row;
            height: 3.75rem;

            &__contact {
                flex: 2;
                background-color: $blue;
                margin-right:1px;
                padding-top: 0.9375rem ;
                padding-bottom: 0.9375rem ;
                padding-right: 4px;
                padding-left: 0.9375rem ;

                .contact-area {
                    color: white;
                    font-size: 0.875rem;
                    display: flex;
                    align-items: center;
                }
            }

            &__menu {
                flex: 8;
                background-color: white;
                height: 3.75rem;
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                // margin-right: 1px;

                .searchBar__links {
                    padding: 0.875rem;

                    li {
                        display: inline-block;
                        padding: 0.5rem 1.125rem;
                        border-right: 0.0625rem solid $light;

                        a {
                            font-size: 0.875rem;
                            color: #7c848b;
                        }

                        &:hover a {
                            color: black;
                            transition: all 0.3s ease-in;
                        }
                    }

                    li:last-child {
                        border: 0;
                    }
                }

                .searchBar__social-links {
                    padding: 0.8125rem;

                    li {
                        display: inline-block;
                        padding: 0.6875rem;

                        a {
                            color: #7c848b;
                            font-size: 0.875rem;
                        }

                        &:hover a {
                            color: black;
                            transition: all 0.3s ease-in;
                        }
                    }
                }


            }
        }

        &__down {
            display: flex;
            flex-direction: row;
            margin-top: 0.0625rem;
            height: 3.0625rem;

            .searchBar__down__select-form {
                -webkit-box-flex: 1;
                -webkit-flex: 1;
                -ms-flex: 1;
                flex: 1;
                background-color: #dfe7ee;
                margin-right: 1px;
                padding: 0.9375rem 0.9375rem;
                background-image: url(../img/arrow.png);
                background-repeat: no-repeat;
                background-position-x: 90%;
                background-position-Y: 50%;


                select {
                    color: #4a525d;
                    text-transform: uppercase;
                    font-size: 0.875rem;
                    border: none;
                    background-color: transparent;
                    -webkit-appearance: none;
                    -moz-appearance: none;
                }

                option {}
            }

            .searchBar__down__button {
                display: flex;
                flex-direction: row;
                flex: 1.9;
                &__first {
                    background-color: #f4903f;
                    a{
                        color: #c45706;
                    }
                }

                &__second {
                    background-color: $dark;
                    a{
                        color: #ffffff;
                    }
                    
                }

                a {
                    .fa.fa-plus-circle {}

                    &:hover {}

                    &:active {}

                    &:focus {}
                }

                a {
                    .fa.fa-search {}

                    &:hover {}

                    &:active {}

                    &:focus {}
                }
            }
        }
    }

}


.home-page__details__footer {
    &__up {
        .searchBar__footer__select-form {
            select {}

            option {}
        }

        .searchBar__footer__select-form__devide {
            &__second {}

            select {
                option {}
            }
        }
    }

    &__down {
        &__select-form {
            input {}

            select {}

            option {}
        }
    }
}




